body {
  position: relative;

  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  padding: 0;

  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
}

.logo {
  text-decoration: none;
}

.bg-wrap-header {
  background: linear-gradient(37.72deg, rgb(0, 61, 133) 20.6%, rgb(184, 0, 39) 76.31%);
  background-repeat: no-repeat;
}

.logo:hover {
  opacity: 70%;
}

.logo:active {
  opacity: 50%;
}

button {
  list-style: none;
  text-decoration: none;

  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.button {
  list-style: none;
  text-decoration: none;

  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.button:hover {
  opacity: 70%;
}

.button:active {
  opacity: 50%;
}

.button:focus {
  opacity: 70%;
}

.bg-wrap {
  background: #f8f9fc;
}

.container-bottom {
  display: flex;
}


.bg-wrap-footer {
  margin-top: auto;

  background-color: #030a1a;
}

.reviews {
  display: none;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

@media (max-width: $desktop-width) {
  .container-bottom {
    flex-direction: column;
    min-height: 675px;

    background: none;
    background-color: #ffffff;
  }
}
